<template>
    <div class='xiangqing'>
        <div class="pro">
            <div class="proXinxi">
                <div>
                    <div class="img">
                        <div class="imgLog"><img :src="imgUrl + proImg" alt=""></div>
                        <div class="imgMin">
                             <div class="imgAll">
                                <img :src="imgUrl + item" alt="" v-for="(item,i) in get_goods_info.pic" :key="i" @click="proImg = get_goods_info.pic[i];ageActive2 = i">
                            </div>
                            <!-- <div class="swiper-button-prev"></div>
                            <div class="swiper-button-next"></div> -->
                        </div>
                        <div class="shoucang" v-if="get_goods_info.goodsKeep == 'no'">
                            <div class="image" @click="shoucang(get_goods_info.id)">
                                <img src="../../assets/img/shangcheng/shoucang.png" alt="">
                            </div>
                            <div @click="shoucang(get_goods_info.id)">
                                {{$t('xiangqing[0]')}}
                            </div>
                        </div>
                        <div class="shoucang" v-if="get_goods_info.goodsKeep == 'yes'">
                            <div class="image" @click="shoucang(get_goods_info.id)">
                                <img src="../../assets/img/shangcheng/shoucangActive.png" alt="">
                            </div>
                            <div @click="shoucang(get_goods_info.id)">
                                {{$t('xiangqing[1]')}}
                            </div>
                        </div>
                    </div>
                    <div class="text">
                        <h1 class="name">{{get_goods_info.name}}</h1>
                        <p class="proText">{{get_goods_info.describe}}</p>
                        <p class="proBianhao"><span>SKU：{{get_goods_info.sku}}</span><span>{{$t('xiangqing[2]')}}：{{get_goods_info.create_time}}</span></p>
                        <div class="price">
                            <div class="left">
                                <div><span>{{$t('xiangqing[3]')}}</span><span>￥</span><span>{{get_goods_info.market_price}}</span></div>
                                <div><span>{{$t('xiangqing[4]')}}</span><span>￥</span><span>{{get_goods_info.sell_price}}</span></div>
                            </div>
                            <div class="right">
                                <div>{{get_goods_info.sell_num}}</div>
                                <div>{{$t('xiangqing[5]')}}</div>
                            </div>
                        </div>
                        <div class="hr"></div>
                        <div class="dizhi formItem">
                            <div class="left">{{$t('xiangqing[6]')}}</div>
                            <div class="right diqu">
                                <!-- 地区选择 -->
                                <div :class="{checked:ageActive == i}"  @click="changeStatus(i,item)" v-for="(item,i) in get_goods_info.delivery_place" :key="i">
                                    {{item}}
                                    <div class="border"></div>
                                </div>
                            </div>
                        </div>
                        <div class="chima formItem">
                            <div class="left">{{$t('xinzeng[7]')}}</div>
                            <div class="right diqu">
                                <!-- 地区选择 -->
                                <div :class="{checked:ageActive1 == i}"  @click="changeStatus1(i,item)" v-for="(item,i) in get_goods_info.attr" :key="i">
                                    {{item}}
                                    <div class="border"></div>
                                </div>
                            </div>
                        </div>
                        <div class="yanse formItem">
                            <div class="left">{{$t('xinzeng[8]')}}</div>
                            <div class="right diqu">
                                <!-- 地区选择 -->
                                <div :class="{checked:ageActive2 == i}"  @click="changeStatus2(i,item)" v-for="(item,i) in get_goods_info.pic" :key="i">
                                    <img :src="imgUrl + item" alt="">
                                    <div class="border"></div>
                                </div>
                            </div>
                        </div>
                        <div class="shuliang formItem">
                            <div class="left">{{$t('xiangqing[7]')}}</div>
                            <div class="right">
                                <el-input-number v-model="num" :min="1" :max="get_goods_info.stock" :label="$t('xiangqing[7]')" size="mini"></el-input-number>
                                 {{$t('xiangqing[8]')}} ({{$t('xiangqing[9]')}}{{get_goods_info.stock}})
                            </div>
                        </div>
                        <!-- <div class="zhifu formItem">
                            <div class="left">支付</div>
                            <div class="right">
                                支付方式
                            </div>
                        </div> -->
                        <div class="btn">
                            <div class="jiagou" @click="lijigoumai(get_goods_info.id)">{{$t('xiangqing[11]')}}</div>
                            <div class="goumai" @click="jiagou(get_goods_info.id,num)">
                                <div></div>
                                {{$t('xiangqing[10]')}}
                            </div>
                        </div>
                    </div>
                </div>
                <div class="tuijian">
                    <h1>一 {{$t('xiangqing[12]')}} 一</h1>
                    <div v-for="(item,i) in get_goods_list" :key="i" @click="xianging(item.id)">
                        <img :src="imgUrl + item.thumb" alt="">
                        <p style="color: #555;font-weight: bold;"><span style="font-size:14px;">¥</span>{{item.sell_price}}</p>
                    </div>
                </div>
            </div>
            <div class="proImg">
                <div class="title">
                    {{$t('xiangqing[13]')}}
                </div>
                <div v-html="get_goods_info.content">
                </div>
            </div>
        </div>
    </div>  
</template>
<script>
export default {
    data() {
        return {
            imgUrl:'',//图片ip
            proImg:'',//商品大图
            get_goods_info:'',//商品详情
            num:'',//商品数量
            get_goods_list:'',//推荐列表
            ageActive:0,
            ageActive1:0,
            ageActive2:0,
        };
    },
    created:function(){
    },
    mounted:function(){
        var _this = this;
        _this.imgUrl = _this.apis.second;
        var mySwiper = new Swiper('.swiper-container', {
            observer:true,//修改swiper自己或子元素时，自动初始化swiper
            observeParents:true,//修改swiper的父元素时，自动初始化swiper
            autoplay: false,//可选选项，自动滑动
            autoplayDisableOnInteraction : false,
            pagination : '.swiper-pagination',
            paginationClickable :false,
            navigation: {
                nextEl: '.swiper-button-next',
                prevEl: '.swiper-button-prev',
            },
        })
        _this.getPro()
        _this.apis.get(_this.apis.get_goods_list,{params:{lang:_this.apis.lang,pageNumber:3,status:1}}).then(function(res) {
            if(res.data.code == 0){
                _this.get_goods_list = res.data.data.data
            }
        })
    },
    watch:{
    },
    methods:{
        //地区选择
        changeStatus(i,item){
            var _this = this;
            _this.ageActive = i
        },
        //尺码选择
        changeStatus1(i){
            var _this = this;
            _this.ageActive1 = i
        },
        //颜色选择
        changeStatus2(i,imgurl){
            var _this = this;
            _this.ageActive2 = i
            _this.proImg = imgurl;
        },
        xianging(id){
            var _this = this;
            _this.getPro(id)
        },
        shoucang(id){
            var _this = this;
            _this.apis.get(_this.apis.collect,{params:{lang:_this.apis.lang,goods_id:id}}).then(function(res) {
                if(res.data.code == 0 ){
                    _this.$message({
                        message: res.data.msg,
                        type: 'success'
                    });
                    _this.getPro();
                }
            })
        },
        //获取商品xiangqing
        getPro(id){
            var _this = this;
            if(id){
                _this.apis.get(_this.apis.get_goods_info,{params:{lang:_this.apis.lang,id:id}}).then(function(res) {
                    if(res.data.code == 0){
                        _this.get_goods_info = res.data.data
                        _this.proImg = res.data.data.pic[0]
                    }
                })
            }else{
                _this.apis.get(_this.apis.get_goods_info,{params:{lang:_this.apis.lang,id:_this.$route.query.id}}).then(function(res) {
                    if(res.data.code == 0){
                        _this.get_goods_info = res.data.data
                        _this.proImg = res.data.data.pic[0]
                    }
                })
            }
        },
        //加入购物车
        jiagou(id,num){
            var _this = this;
            _this.$confirm(_this.$t('xiangqing[14]'), _this.$t('xiangqing[15]'), {
                confirmButtonText: _this.$t('xiangqing[16]'),
                cancelButtonText: _this.$t('xiangqing[17]'),
                type: 'warning'
            }).then(() => {
                _this.apis.post(_this.apis.add_car,{lang:_this.apis.lang,goods_id:id,num:num,attr:_this.get_goods_info.attr[_this.ageActive1],color:_this.get_goods_info.pic[_this.ageActive2],delivery_place:_this.get_goods_info.delivery_place[_this.ageActive],}).then(function(res) {
                    if(res.data.code == 0){
                        _this.$message({
                            type: 'success',
                            message: _this.$t('xiangqing[18]')
                        });
                    }
                })
            }).catch(() => {
                _this.$message({
                    type: 'info',
                    message: res.data.msg
                });
            });
        },
        //立即购买
        lijigoumai(id){
            var _this = this;
            var data = {
                delivery_place:_this.get_goods_info.delivery_place[_this.ageActive],
                attr:_this.get_goods_info.attr[_this.ageActive1],
                color:_this.get_goods_info.pic[_this.ageActive2],
                num:_this.num,
                type:1,
                id:id,
                lang:_this.apis.lang,
            }
            _this.apis.post(_this.apis.buy,data).then(function(res) {
                if(res.data.code == 0){
                    _this.$router.push({path:'querendingdan',query:{title:_this.$t('xiangqing[19]'),id:_this.$route.query.id,num:_this.num,data:res.data.data,}})
                }
            })
        },
    },
    components:{
    }
}
</script>
<style lang='less' scoped>
.xiangqing{
    .pro{
        width: 1200px;
        margin: 0 auto;
        .proXinxi{
            display: flex;
            justify-content: space-between;
            &>div:nth-child(1){
                flex: 1;
                display: flex;
                justify-content: space-between;
                align-items: top;
                .img{
                    .imgLog{
                        width: 430px;
                        height: 430px;
                        margin-bottom: 23px;
                    }
                    .imgMin{
                        width: 430px;
                        position: relative;
                        .imgAll::-webkit-scrollbar {/*滚动条整体样式*/
                            width: 5px;     /*高宽分别对应横竖滚动条的尺寸*/
                            height: 7px;
                            // width: 0px;     /*高宽分别对应横竖滚动条的尺寸*/
                            // height: 0px;
                        }
                        .imgAll::-webkit-scrollbar-thumb {/*滚动条里面小方块*/
                            -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.1);
                            background: #d7d7d7;
                        }
                        .imgAll::-webkit-scrollbar-track {/*滚动条里面轨道*/
                            -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.05);
                            border-radius: 10px;
                            background: #EDEDED;
                        }
                        .imgAll{
                            width: 100%;
                            padding-bottom: 10px;
                            display: flex;
                            flex-wrap: nowrap;
                            overflow-x: scroll;
                            overflow-y: hidden;
                            img{
                                width: 62px;
                                height: 62px;
                                margin: 0 9.9px;
                            }
                        }
                        .swiper-button-prev{
                            width: 20px;
                            height: 62px;
                            margin-top: 0 ;
                            padding: 5px;
                            background-image: url(../../assets/img/shangcheng/blue.png);
                            background-size: 100% 100%;
                            top: 0;
                        }
                        .swiper-button-next{
                            width: 20px;
                            height: 62px;
                            margin-top: 0 ;
                            padding: 5px;
                            top: 0;
                            background-image: url(../../assets/img/shangcheng/blue.png);
                            background-size: 100% 100%;
                        }
                    }
                    .shoucang{
                        display: flex;
                        align-items: center;
                        line-height: 20px;
                        color: #777777;
                        div{
                            cursor:pointer;
                        }
                        .image{
                            width: 13px;
                            height: 13px;
                            margin-right: 5px;
                            img{
                                width: 13px;
                                height: 13px;
                            }
                        }
                    }
                }
                .text{
                    flex: 1;
                    margin: 0 34px;
                    h1{
                        color: #1F1F1F;
                        font-size: 18px;
                        margin: 0 12px;
                        margin-bottom: 10px;
                        -webkit-line-clamp:2;
                        -webkit-box-orient: vertical;
                        display: -webkit-box;
                        text-overflow: ellipsis;
                        overflow: hidden;
                    }
                    .proText{
                        margin: 0 12px;
                        font-size: 12px;
                        color: #898989;
                        -webkit-line-clamp:3;
                        -webkit-box-orient: vertical;
                        display: -webkit-box;
                        text-overflow: ellipsis;
                        overflow: hidden;
                    }
                    .proBianhao{
                        margin: 0 12px;
                        margin-top: 10px;
                        padding-bottom: 5px;
                        border-bottom: 1px solid #E6E6E6;
                        &>span:nth-child(1){
                            color: #FF8C52;
                            font-size: 13px;
                            margin-right: 50px;
                        }
                        &>span:nth-child(2){
                            color: #5E5D5D;
                            font-size: 13px;
                        }
                    }
                    .price{
                        background: #FFF2E8;
                        margin-top: 13px;
                        padding: 13px 6px;
                        display: flex;
                        justify-content: space-between;
                        align-items: center;
                        .left{
                            line-height: 30px;
                            &>div:nth-child(1){
                                font-weight: bold;
                                &>span:nth-child(1){
                                    display: inline-block;
                                    width: 62px;
                                    color: #696969;
                                    font-size: 12px;
                                }
                                &>span:nth-child(2){
                                    color: #4A4949;
                                    font-size: 14px;
                                }
                                &>span:nth-child(3){
                                    color: #4A4949;
                                    font-size: 14px;
                                }
                            }
                            &>div:nth-child(2){
                                color: #FF5A00;
                                font-weight: bold;
                                &>span:nth-child(1){
                                    display: inline-block;
                                    width: 62px;
                                    font-size: 14px;
                                }
                                &>span:nth-child(2){
                                    font-size: 18px;
                                }
                                &>span:nth-child(3){
                                    font-size: 28px;
                                }
                            }
                        }
                        .right{
                            line-height: 20px;
                            color: #979797;
                            text-align: center;
                            &>div:nth-child(1){
                                font-size: 18px;
                            }
                            &>div:nth-child(2){
                                font-size: 12px;
                            }
                        }
                    }
                    .hr{
                        height: 1px;
                        margin: 0 12px;
                        margin-top: 13px;
                        background: #E6E6E6;
                    }
                    .dizhi{
                        height: 50px;
                        margin: 0 12px;
                        border-bottom: 1px solid #E6E6E6;
                    }
                    .shuliang{
                        height: 76px;
                        margin: 0 12px;
                        margin-bottom: 50px;
                        .el-input__inner{
                            border-radius: 0px;
                        }
                    }
                    .zhifu{
                        height: 87px;
                        margin: 0 12px;
                    }
                    .btn{
                        text-align: right;
                        div{
                            font-size: 16px;
                            color: #FFFFFF;
                            display: inline-block;
                            width: 184px;
                            height: 42px;
                            text-align: center;
                            line-height: 42px;
                            background: #FFAD01;
                            border: 1px solid #FFAD01;
                            cursor:pointer;  //鼠标悬浮变手
                        }
                        .goumai{
                            div{
                                display: inline-block;
                                width: 20px;
                                height: 18px;
                                margin-top: 3px;
                                background-image: url(../../assets/img/shangcheng/gouwuche.png);
                                background-size: 100% 100%;
                            }
                        }
                        .jiagou{
                            margin-right: 15px;
                            // font-weight: bold;
                            font-size: 16px;
                            color: #ff6c00;
                            border: 1px solid #FFAD01;
                            background: #FFF2E8;
                        }
                    }
                    .chima{
                        height: 50px;
                        margin: 0 12px;
                        border-bottom: 1px solid #E6E6E6;
                    }
                    .yanse{
                        height: 50px;
                        margin: 0 12px;
                        border-bottom: 1px solid #E6E6E6;
                        .diqu{
                            &>div{
                                width: 40px !important;
                                height: 40px !important;
                            }
                        }
                    }
                    .formItem{
                        height: 61px;
                        display: flex;
                        justify-content: space-between;
                        align-items: center;
                        .left{
                            width: 65px;
                        }
                        .right{
                            flex: 1;
                        }
                        .diqu{
                            display: flex;
                            justify-content: left;
                            align-items: center;
                            .checked{
                                border: 2px solid #ff5a00;
                                .border{
                                    display: block;
                                }
                            }
                            div{
                                color:#4A4949;
                                font-size: 14px;
                                font-weight: bold;
                                width: 65px;
                                height: 32px;
                                margin-right: 18px;
                                text-align: center;
                                line-height: 32px;
                                border: 1px solid #C2C2C2;
                                position: relative;
                                box-sizing: border-box;
                                .border{
                                    width: 17px;
                                    height: 15px;
                                    display: none;
                                    border: none;
                                    margin-right: 0;
                                    position: absolute;
                                    right: 0;
                                    bottom: 0;
                                    background-image: url(../../assets/img/shangcheng/xuanzhong.png);
                                    background-size: 100% 100%;
                                }
                            }
                        }
                    }
                }
            }
            &>div:nth-child(2){
                width: 150px;
                height: 515px;
                overflow-y: hidden;
                h1{
                    text-align: center;
                    margin-bottom: 10px;
                    color: #8B8B8B;
                    font-size: 12px;
                }
                div{
                    position: relative;
                    width: 150px;
                    height: 150px;
                    margin: 16px 0;
                    cursor:pointer;
                    p{
                        width: 100%;
                        height: 26px;
                        line-height: 26px;
                        text-align: center;
                        background: rgba(255, 255, 255, 0.7);
                        position: absolute;
                        bottom: 0;
                        -webkit-line-clamp:1;
                        -webkit-box-orient: vertical;
                        display: -webkit-box;
                        text-overflow: ellipsis;
                        overflow: hidden;
                    }
                }
            }
        }
        .proImg{
            margin-top: 70px;
            margin-bottom: 30px;
            .title{
                color: #FF6C00;
                font-size: 20px;
                font-weight: bold;
                margin: 0 182px;
                padding-bottom: 35px;
                margin-bottom: 35px;
                text-align: center;
                border-bottom: 1px solid #E6E6E6;
            }
        }
    }
}
</style>


